export default class ServerAPI {
    constructor() {
        // baseUrl = new URL('https://react-learnwords-example.herokuapp.com/');
        this.baseUrl = new URL('https://rslang-app-2022.herokuapp.com/');
        // /users/settings and /users/{id}/aggregatedWords are NOT IMPLEMENTED
        // /signin
        // .
        // signin
        // /words
        // .
        // getWords
        // getWordByWordId
        // /users
        // .
        // createUser
        // getUser
        // updateUser
        // deleteUser
        // getNewUserTokens (use refreshToken instead of token in request header)
        // /users/{id}/words
        // .
        // getUserWords
        // createUserWord ("optional" property in the body request is NOT DEFINED)
        // getUserWordByWordId
        // updateUserWord ("optional" property in the body request is NOT DEFINED)
        // deleteUserWord
        // /users/{id}/statistics
        // .
        // getStatistics
        // createStatistics ("optional" property in the body request is NOT DEFINED)
        this.signIn = async ({ email, password }) => {
            const url = new URL('signin', this.baseUrl);
            const body = { email, password };
            const response = await fetch(url.href, {
                method: 'POST',
                headers: {
                    Accept: 'application/json',
                    'Content-Type': 'application/json'
                },
                body: JSON.stringify(body)
            });
            const content = await response.json();
            return content;
        };
        this.getWords = async ({ group, page }) => {
            const url = new URL('words', this.baseUrl);
            url.searchParams.set('group', String(group));
            url.searchParams.set('page', String(page));
            const response = await fetch(url.href, {
                method: 'GET',
                headers: {
                    Accept: 'application/json'
                }
            });
            const content = await response.json();
            return content;
        };
        this.getWordByWordId = async ({ wordId }) => {
            const url = new URL(`words/${wordId}`, this.baseUrl);
            const response = await fetch(url.href, {
                method: 'GET',
                headers: {
                    Accept: 'application/json'
                }
            });
            const content = await response.json();
            return content;
        };
        this.createUser = async (user) => {
            const url = new URL('users', this.baseUrl);
            const response = await fetch(url.href, {
                method: 'POST',
                headers: {
                    Accept: 'application/json',
                    'Content-Type': 'application/json'
                },
                body: JSON.stringify(user)
            });
            const content = await response.json();
            return content;
        };
        this.getUser = async ({ token, id }) => {
            const url = new URL(`users/${id}`, this.baseUrl);
            const response = await fetch(url.href, {
                method: 'GET',
                headers: {
                    Authorization: `Bearer ${token}`,
                    Accept: 'application/json'
                }
            });
            const content = await response.json();
            return content;
        };
        this.updateUser = async ({ token, id, email, password }) => {
            const url = new URL(`users/${id}`, this.baseUrl);
            const body = { email, password };
            const response = await fetch(url.href, {
                method: 'PUT',
                headers: {
                    Authorization: `Bearer ${token}`,
                    Accept: 'application/json',
                    'Content-Type': 'application/json'
                },
                body: JSON.stringify(body)
            });
            const content = await response.json();
            return content;
        };
        this.deleteUser = async ({ token, id }) => {
            const url = new URL(`users/${id}`, this.baseUrl);
            const response = await fetch(url.href, {
                method: 'DELETE',
                headers: {
                    Authorization: `Bearer ${token}`,
                    Accept: '*/*'
                }
            });
            const content = await response.json();
            return content;
        };
        this.getNewUserTokens = async ({ token, id }) => {
            const url = new URL(`users/${id}/tokens`, this.baseUrl);
            const response = await fetch(url.href, {
                method: 'GET',
                headers: {
                    Authorization: `Bearer ${token}`,
                    Accept: 'application/json'
                }
            });
            const content = await response.json();
            return content;
        };
        this.getUserWords = async ({ token, id }) => {
            const url = new URL(`users/${id}/words`, this.baseUrl);
            const response = await fetch(url.href, {
                method: 'GET',
                headers: {
                    Authorization: `Bearer ${token}`,
                    Accept: 'application/json'
                }
            });
            const content = await response.json();
            return content;
        };
        this.createUserWord = async ({ token, id, wordId, difficulty, optional }) => {
            const url = new URL(`users/${id}/words/${wordId}`, this.baseUrl);
            const body = {
                difficulty,
                optional
            };
            const response = await fetch(url.href, {
                method: 'POST',
                headers: {
                    Authorization: `Bearer ${token}`,
                    Accept: 'application/json',
                    'Content-Type': 'application/json'
                },
                body: JSON.stringify(body)
            });
            const content = await response.json();
            return content;
        };
        this.getUserWordByWordId = async ({ token, id, wordId }) => {
            const url = new URL(`users/${id}/words/${wordId}`, this.baseUrl);
            const response = await fetch(url.href, {
                method: 'GET',
                headers: {
                    Authorization: `Bearer ${token}`,
                    Accept: 'application/json'
                }
            });
            const content = await response.json();
            return content;
        };
        this.updateUserWord = async ({ token, id, wordId, difficulty, optional }) => {
            const url = new URL(`users/${id}/words/${wordId}`, this.baseUrl);
            const body = {};
            if (difficulty) {
                body.difficulty = difficulty;
            }
            if (optional) {
                body.optional = optional;
            }
            const response = await fetch(url.href, {
                method: 'PUT',
                headers: {
                    Authorization: `Bearer ${token}`,
                    Accept: 'application/json',
                    'Content-Type': 'application/json'
                },
                body: JSON.stringify(body)
            });
            const content = await response.json();
            return content;
        };
        this.deleteUserWord = async ({ token, id, wordId }) => {
            const url = new URL(`users/${id}/words/${wordId}`, this.baseUrl);
            const response = await fetch(url.href, {
                method: 'DELETE',
                headers: {
                    Authorization: `Bearer ${token}`,
                    Accept: 'application/json'
                }
            });
            const content = await response.json();
            return content;
        };
        this.getStatistics = async ({ token, id }) => {
            const url = new URL(`users/${id}/statistics`, this.baseUrl);
            const response = await fetch(url.href, {
                method: 'GET',
                headers: {
                    Authorization: `Bearer ${token}`,
                    Accept: 'application/json'
                }
            });
            const content = await response.json();
            return content;
        };
        this.createStatistics = async ({ token, id, optional }) => {
            const url = new URL(`users/${id}/statistics`, this.baseUrl);
            const body = { optional };
            const response = await fetch(url.href, {
                method: 'PUT',
                headers: {
                    Authorization: `Bearer ${token}`,
                    Accept: 'application/json',
                    'Content-Type': 'application/json'
                },
                body: JSON.stringify(body)
            });
            const content = await response.json();
            return content;
        };
    }
}
