import Sprint from './sprint/sprint';
import AudioCall from './audioCall/audioCall';
import GameResult from './gameResult/gameResult';
export default class Games {
    constructor(serverAPI, localStorageAPI, contentURL) {
        this.innerHtmlTemplate = `
    <div class="wrapper wrapper_padding">
      <div class="games__input-container">
        <input type="radio" name="gameName" value="sprint" id="sprintRadio">
        <input type="radio" name="gameName" value="audioCall" id="audioCallRadio">
        <label for="sprintRadio">
          <img class="games__image games__image_simple" src="./assets/svg/sprint-game.svg" alt="">
          <img class="games__image games__image_color" src="./assets/svg/sprint-game-color.svg" alt="">
          <h3 class="games__name"></h3>
        </label>
        <div class="games__controls-container">
        <button class="games__start" disabled></button>
        <select class="games__select" name="gameLevel">
          <option value="0"> 1</option>
          <option value="1"> 2</option>
          <option value="2"> 3</option>
          <option value="3"> 4</option>
          <option value="4"> 5</option>
          <option value="5"> 6</option>
        </select>
      </div>
        <label for="audioCallRadio">
          <img class="games__image games__image_simple" src="./assets/svg/audioCall-game.svg" alt="">
          <img class="games__image games__image_color" src="./assets/svg/audioCall-game-color.svg" alt="">
          <h3 class="games__name"></h3>
        </label>
      </div>
    </div>
  `;
        this.showComponent = () => {
            const contentElem = document.querySelector('.content');
            contentElem.append(this.componentElem);
        };
        this.hideComponent = () => {
            document.querySelector('.games')?.remove();
        };
        this.componentElem = document.createElement('div');
        this.serverAPI = serverAPI;
        this.localStorageAPI = localStorageAPI;
        this.contentURL = contentURL;
        this.gameResult = new GameResult(this.serverAPI, this.localStorageAPI, this.contentURL);
        this.sprint = new Sprint(this.serverAPI, this.localStorageAPI, this.contentURL, this.gameResult);
        this.audioCall = new AudioCall(this.serverAPI, this.localStorageAPI, this.contentURL, this.gameResult);
    }
    createThisComponent() {
        this.componentElem.innerHTML = this.innerHtmlTemplate;
        this.componentElem.classList.add('games');
    }
    createComponent() {
        this.createThisComponent();
        this.gameResult.createComponent();
        this.sprint.createComponent();
        this.audioCall.createComponent();
    }
    setThisListeners(setAnimatedBgTheme) {
        this.listenerForGameRadioBtns(setAnimatedBgTheme);
        this.listenerForStartBtn();
    }
    setListeners(setAnimatedBgTheme) {
        this.setThisListeners(setAnimatedBgTheme);
        this.gameResult.setListeners();
    }
    getGameInfo() {
        const gameNameInput = this.componentElem.querySelector('input[name="gameName"]:checked');
        const gameLevelSelect = this.componentElem.querySelector('select[name="gameLevel"]');
        const gameName = gameNameInput.value;
        const gameLevel = +gameLevelSelect.value;
        return [gameName, gameLevel];
    }
    listenerForGameRadioBtns(setAnimatedBgTheme) {
        const gameRadioInputContainer = this.componentElem.querySelector('.games__input-container');
        gameRadioInputContainer.addEventListener('click', () => {
            const radioBtns = gameRadioInputContainer.querySelectorAll('input');
            const radioBtnsArr = Array.from(radioBtns);
            if (radioBtnsArr.some((el) => el.checked)) {
                const startBtn = this.componentElem.querySelector('.games__start');
                startBtn.removeAttribute('disabled');
                const gameName = this.getGameInfo()[0];
                setAnimatedBgTheme(gameName);
            }
        });
    }
    listenerForStartBtn() {
        const startBtn = this.componentElem.querySelector('.games__start');
        startBtn.addEventListener('click', () => {
            const [gameName, gameLevel] = this.getGameInfo();
            if (gameName === 'sprint') {
                this.sprint.startGame(+gameLevel);
            }
            else if (gameName === 'audioCall') {
                this.audioCall.startGame(+gameLevel);
            }
        });
    }
}
