export default class SignUp {
    constructor(serverAPI, localStorageAPI, parentComponentElem) {
        this.innerHtmlTemplate = `
    <h2>  </h2>
    <div class="account__error-box"></div>
    <form>
      <input class="account__input account__input_name" type="text" placeholder="" required>
      <input class="account__input account__input_email" type="email" placeholder="email" autocomplete="email" pattern="[a-z0-9._%+-]+@[a-z0-9.-]+.[a-z]{1,}$" required>
      <input class="account__input account__input_password" type="password" placeholder="" minlength="8" autocomplete="new-password" required>
      <button type="button" class="signUp__btn_signUp"></button>
    </form>
  `;
        this.showComponent = () => {
            const accountContent = this.parentComponentElem.querySelector('.account__content');
            accountContent.append(this.componentElem);
        };
        this.componentElem = document.createElement('div');
        this.serverAPI = serverAPI;
        this.localStorageAPI = localStorageAPI;
        this.parentComponentElem = parentComponentElem;
    }
    createThisComponent() {
        this.componentElem.innerHTML = this.innerHtmlTemplate;
        this.componentElem.classList.add('signUp');
    }
    createComponent() {
        this.createThisComponent();
    }
    setThisListeners(updateHeader) {
        const signUpBtn = this.componentElem.querySelector('.signUp__btn_signUp');
        signUpBtn.addEventListener('click', () => {
            const inputValues = this.getInputValues();
            if (inputValues === null) {
                return;
            }
            const { name, email, password } = inputValues;
            this.serverAPI
                .createUser({
                name,
                email,
                password
            })
                .then(async () => {
                const authorizationContent = await this.serverAPI.signIn({ email, password });
                await this.localStorageAPI.fillAccountStorage(authorizationContent, password);
                document.querySelector('#account')?.dispatchEvent(new Event('click'));
                // eslint-disable-next-line
                const { isLoggedIn, name } = this.localStorageAPI.accountStorage;
                updateHeader(isLoggedIn, name);
            })
                .catch(() => {
                this.showValidationError('   "email"  ');
            });
        });
        const formInputs = this.componentElem.querySelectorAll('.account__input');
        formInputs.forEach((input) => {
            input.addEventListener('input', () => {
                if (!input.validity.valid) {
                    input.classList.add('invalid');
                }
                else {
                    input.classList.remove('invalid');
                }
            });
        });
    }
    setListeners(updateHeader) {
        this.setThisListeners(updateHeader);
    }
    getInputValues() {
        const nameInput = this.componentElem.querySelector('.account__input_name');
        const emailInput = this.componentElem.querySelector('.account__input_email');
        const passwordInput = this.componentElem.querySelector('.account__input_password');
        const name = nameInput.value;
        const email = emailInput.value;
        const password = passwordInput.value;
        const isValuesValid = this.validationInputValues({ name, email, password });
        if (!isValuesValid) {
            return null;
        }
        nameInput.value = '';
        emailInput.value = '';
        passwordInput.value = '';
        return { name, email, password };
    }
    validationInputValues({ name, email, password }) {
        let nameValidation = true;
        let emailValidation = true;
        let passwordValidation = true;
        if (name.length === 0) {
            nameValidation = false;
        }
        if (password.length <= 7) {
            passwordValidation = false;
        }
        const hasAtSymbol = email.includes('@');
        if (!hasAtSymbol) {
            emailValidation = false;
        }
        else {
            const emailParts = email.split('@');
            const hasDot = emailParts[1].includes('.');
            if (emailParts[0].length === 0) {
                emailValidation = false;
            }
            else if (!hasDot) {
                emailValidation = false;
            }
            else if (emailParts[1].split('.')[0].length === 0) {
                emailValidation = false;
            }
            else if (emailParts[1].split('.')[1].length === 0) {
                emailValidation = false;
            }
        }
        if (!nameValidation) {
            this.showValidationError('     1 ');
        }
        else if (!emailValidation) {
            this.showValidationError('Email   : example@mail.com');
        }
        else if (!passwordValidation) {
            this.showValidationError('     8 ');
        }
        return [nameValidation, emailValidation, passwordValidation].every((isValid) => isValid === true);
    }
    showValidationError(errorText) {
        const errorBox = this.componentElem.querySelector('.account__error-box');
        errorBox.textContent = errorText;
    }
}
